* {
            margin: 0;
            padding: 0;
            box-sizing: border-box;
        }

        body {
            font-family: 'Georgia', serif;
            background-color: #0a0a0a;
            color: #ffffff;
            line-height: 1.6;
            overflow-x: hidden;
        }

        .container {
            max-width: 1200px;
            margin: 0 auto;
            padding: 0 20px;
        }

        /* Header */
        .header {
            position: fixed;
            top: 0;
            width: 100%;
            background-color: rgba(10, 10, 10, 0.95);
            backdrop-filter: blur(10px);
            z-index: 1000;
            padding: 20px 0;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        }

        .nav {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .logo {
            font-size: 24px;
            font-weight: bold;
            color: #ffffff;
            text-decoration: none;
        }

        .nav-links {
            display: flex;
            gap: 40px;
            list-style: none;
        }

        .nav-links a {
            color: #ffffff;
            text-decoration: none;
            font-size: 16px;
            transition: opacity 0.3s ease;
        }

        .nav-links a:hover {
            opacity: 0.7;
        }

        /* Hero Section */
        .hero {
            height: 100vh;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%);
            position: relative;
        }

        .hero::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: url('data:image/svg+xml,<svg width="100" height="100" xmlns="http://www.w3.org/2000/svg"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="50" cy="50" r="1" fill="%23ffffff" fill-opacity="0.02"/></pattern></defs><rect width="100%" height="100%" fill="url(%23grain)"/></svg>');
            opacity: 0.3;
        }

        .hero-content {
            position: relative;
            z-index: 2;
        }

        .hero h1 {
            font-size: clamp(48px, 8vw, 120px);
            font-weight: 300;
            letter-spacing: -2px;
            margin-bottom: 20px;
            background: linear-gradient(45deg, #ffffff, #cccccc);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }

        .hero-subtitle {
            font-size: clamp(18px, 3vw, 24px);
            color: #888888;
            margin-bottom: 40px;
            max-width: 600px;
            margin-left: auto;
            margin-right: auto;
        }

        .location {
            font-size: 16px;
            color: #666666;
            margin-bottom: 40px;
        }

        .contact-info {
            display: flex;
            gap: 30px;
            justify-content: center;
            flex-wrap: wrap;
        }

        .contact-item {
            color: #ffffff;
            text-decoration: none;
            padding: 10px 20px;
            border: 1px solid rgba(255, 255, 255, 0.2);
            border-radius: 25px;
            transition: all 0.3s ease;
            font-size: 14px;
        }

        .contact-item:hover {
            background-color: #ffffff;
            color: #0a0a0a;
        }

        /* Section Styles */
        .section {
            padding: 100px 0;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        }

        .section h2 {
            font-size: clamp(32px, 5vw, 48px);
            font-weight: 300;
            margin-bottom: 60px;
            text-align: center;
            color: #ffffff;
        }

        /* About Section */
        .about-content {
            max-width: 800px;
            margin: 0 auto;
            text-align: center;
        }

        .about-text {
            font-size: 20px;
            line-height: 1.8;
            color: #cccccc;
            margin-bottom: 40px;
        }

        /* Experience Section */
        .experience-grid {
            display: grid;
            gap: 60px;
            max-width: 900px;
            margin: 0 auto;
        }

        .job {
            padding: 40px;
            background-color: rgba(255, 255, 255, 0.02);
            border-radius: 8px;
            border: 1px solid rgba(255, 255, 255, 0.1);
            transition: all 0.3s ease;
        }

        .job:hover {
            background-color: rgba(255, 255, 255, 0.05);
            transform: translateY(-5px);
        }

        .job-header {
            margin-bottom: 20px;
        }

        .job-title {
            font-size: 24px;
            color: #ffffff;
            margin-bottom: 10px;
        }

        .job-company {
            font-size: 18px;
            color: #888888;
            margin-bottom: 5px;
        }

        .job-period {
            font-size: 14px;
            color: #666666;
        }

        .job-description ul {
            list-style: none;
            margin-top: 20px;
        }

        .job-description li {
            margin-bottom: 12px;
            color: #cccccc;
            position: relative;
            padding-left: 20px;
        }

        .job-description li::before {
            content: 'â€¢';
            color: #ffffff;
            position: absolute;
            left: 0;
        }

        /* Skills Section */
        .skills-grid {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
            gap: 40px;
            margin-top: 60px;
        }

        .skill-category {
            background-color: rgba(255, 255, 255, 0.02);
            padding: 30px;
            border-radius: 8px;
            border: 1px solid rgba(255, 255, 255, 0.1);
        }

        .skill-category h3 {
            font-size: 20px;
            color: #ffffff;
            margin-bottom: 20px;
        }

        .skill-category p {
            color: #cccccc;
            line-height: 1.6;
        }

        /* Education Section */
        .education-content {
            max-width: 600px;
            margin: 0 auto;
            text-align: center;
        }

        .education-item {
            background-color: rgba(255, 255, 255, 0.02);
            padding: 40px;
            border-radius: 8px;
            border: 1px solid rgba(255, 255, 255, 0.1);
        }

        .degree {
            font-size: 24px;
            color: #ffffff;
            margin-bottom: 10px;
        }

        .institution {
            font-size: 18px;
            color: #888888;
            margin-bottom: 5px;
        }

        .graduation-date {
            font-size: 14px;
            color: #666666;
        }

        /* Footer */
        .footer {
            padding: 60px 0;
            text-align: center;
            background-color: rgba(255, 255, 255, 0.02);
        }

        .footer p {
            color: #666666;
            margin-bottom: 20px;
        }

        .footer-links {
            display: flex;
            gap: 30px;
            justify-content: center;
        }

        .footer-links a {
            color: #ffffff;
            text-decoration: none;
            transition: opacity 0.3s ease;
        }

        .footer-links a:hover {
            opacity: 0.7;
        }

        /* Responsive Design */
        @media (max-width: 768px) {
            .nav-links {
                display: none;
            }

            .contact-info {
                flex-direction: column;
                align-items: center;
            }

            .section {
                padding: 60px 0;
            }

            .skills-grid {
                grid-template-columns: 1fr;
            }

            .job {
                padding: 30px 20px;
            }
        }

        /* Smooth scrolling */
        html {
            scroll-behavior: smooth;
        }

        /* Animation for scroll reveal */
        .fade-in {
            opacity: 0;
            transform: translateY(30px);
            transition: all 0.6s ease;
        }

        .fade-in.visible {
            opacity: 1;
            transform: translateY(0);
        }